.how-it-works_inner {
	margin: 0 auto;
	max-width: 80rem;
	padding: 3.625rem 1rem;
}

.how-it-works__section-title {
	color: var(--wp--preset--color--primary);
	font-size: var(--wp--preset--font-size--extra-large);
	font-weight: 600;
	margin-bottom: 3rem;
	text-align: center;
	text-transform: uppercase;
}

.how-it-works__items {
	display: grid;
	gap: 2rem;
	grid-template-columns: 1fr;
}

.how-it-works__item {
	background-color: var(--wp--preset--color--tertiary);
	border-radius: 10px;
	padding: 2.5rem 0;
	text-align: center;
}

.how-it-works__icon {
	display: flex;
	justify-content: center;
	margin-bottom: 1.5rem;
}

.how-it-works__icon img {
	height: 12.5rem;
	object-fit: contain;
	width: 12.5rem;
}

.how-it-works__title {
	color: var(--wp--preset--color--neutral-black);
	font-size: 1.25rem;
	font-weight: 600;
	margin-bottom: 1rem;
}

.how-it-works__description {
	color: var(--wp--preset--color--neutral-black);
	line-height: 20px;
}

@media (min-width: 640px) {
	.how-it-works_inner {
		padding-left: 1.5rem;
		padding-right: 1.5rem;
	}
}

@media (min-width: 768px) {
	.how-it-works__items {
		grid-template-columns: repeat(2, 1fr);
	}
}

@media (min-width: 1024px) {
	.how-it-works_inner {
		padding-bottom: 4rem;
		padding-left: 2rem;
		padding-right: 2rem;
		padding-top: 4rem;
	}

	.how-it-works__items {
		gap: 3rem;
		grid-template-columns: repeat(3, 1fr);
	}
}
